.btn, .btn:link, .btn:visited {
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.5rem 4rem;
  display: inline-block;
  /* inline element is treated as if it is text, hence the properties of text can be applied to it */
  border-radius: 10rem;
  /*high values make the border-radius circular*/
  transition: all .2s;
  position: relative;
  font-size: 1.6rem;
  /*changes for the button*/
  cursor: pointer;
}

.btn:hover {
  transform: translateY(-0.3px);
}

.btn:active {
  outline: none;
  transform: translateY(-1px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

.btn--hollow {
  color: #00B7C3;
  border: 2px solid #00B7C3;
}

.btn--animated {
  animation: moveInBottom .3s ease-out .75s;
  /* styles of 0% will automatically apply before the animation starts */
  animation-fill-mode: backwards;
}

.btn::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 10rem;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all .4s;
}

.btn:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}

.btn-text:link, .btn-text:visited {
  font-size: 1.6rem;
  color: #00B7C3;
  display: inline-block;
  text-decoration: none;
  border-bottom: 1px solid #00B7C3;
  padding: .3rem;
}

.btn-text:hover {
  background-color: #00B7C3;
  color: #fff;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
  transform: translateY(-0.2rem);
}

.btn-text:active {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
  transform: translateY(0);
}

