/**
 * to use table we assign display table to parent div
 * display table-cell to child divs
 */
.popup {
  height: 100vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 300;
  /*for animation*/
  opacity: 0;
  visibility: hidden;
  transition: all .3s;
  /**
    * here in transition delay of .2s is introduced so that
    * first the black background will appear and then the content
  */
}

.popup__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 75%;
  box-shadow: 0 2rem 4rem rgba(0, 0, 0, 0.2);
  z-index: 350;
  background-color: #fff;
  border-radius: .3rem;
  display: table;
  overflow: hidden;
  /*for animation*/
  opacity: 0;
  transform: translate(-50%, -50%) scale(0.25);
  transition: all .4s .2s;
}

.popup:target {
  opacity: 1;
  visibility: visible;
}

.popup:target .popup__content {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}

.popup__left {
  width: 33.333%;
  display: table-cell;
}

.popup__right {
  width: 66.667%;
  display: table-cell;
  vertical-align: middle;
  padding: 3rem 5rem;
}

.popup__img {
  display: block;
  width: 100%;
}

.popup__text {
  font-size: 1.4rem;
  margin-bottom: 4rem;
  /*create provided no. of columns for the text*/
  column-count: 2;
  column-gap: 4rem;
  column-rule: 1px solid #eee;
  hyphens: auto;
}

.popup__close:link, .popup__close:visited {
  color: #777;
  position: absolute;
  top: 2.5rem;
  right: 2.5rem;
  font-size: 3rem;
  text-decoration: none;
  display: inline-block;
  line-height: 1;
  cursor: pointer;
  transition: all .2s;
}

.popup__close:hover {
  color: #00B7C3;
}

.row {
  max-width: 114rem;
  /* trick to align center one block into another */
  margin: 0 auto;
  /* selects everything except the last child */
  /**
   * if all the child elements of div are floating them
   * the height of the div becomes zero.
   * fixed using the clearfix
   */
  /* selects all the elements whose class attribute equals 'col-1' [class="col-1"] */
  /* selects all the elements whose class attribute starts with 'col-1' [class^="col-1"] */
  /* selects all the elements whose class attribute ends with 'col-1' [class$="col-1"] */
  /* selects all the elements whose class attribute contains 'col-1' [class*="col-1"] */
}

.row:not(:last-child) {
  margin-bottom: 8rem;
}

@media (max-width: 56.25em) {
  .row:not(:last-child) {
    margin-bottom: 6rem;
  }
}

@media (max-width: 56.25em) {
  .row {
    max-width: 50rem;
  }
}

.row::after {
  content: "";
  display: table;
  clear: both;
}

.row [class^="col-"] {
  float: left;
}

.row [class^="col-"]:not(:last-child) {
  margin-right: 6rem;
}

@media (max-width: 56.25em) {
  .row [class^="col-"]:not(:last-child) {
    margin-right: 0;
    margin-bottom: 6rem;
  }
}

@media (max-width: 56.25em) {
  .row [class^="col-"] {
    width: 100% !important;
  }
}

.row .col-1-of-2 {
  width: calc((100% - 6rem)/2);
}

.row .col-1-of-3 {
  width: calc((100% - 2 * 6rem)/3);
}

.row .col-1-of-4 {
  width: calc((100% - 3 * 6rem)/4);
}

.row .col-2-of-3 {
  width: calc( 2 * ((100% - 2 * 6rem)/3) + 6rem);
}

.row .col-2-of-4 {
  width: calc( 2 * ((100% - 3 * 6rem)/4) + 6rem);
}

.row .col-3-of-4 {
  width: calc( 3 * ((100% - 3 * 6rem)/4) + 2 * 6rem);
}

