  object-position: center center;
}

.video-container .video::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.7)) !important;
}

.video-container .scroll-btn {
  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translateX(-50%);
  animation-duration: 1.5s;
  cursor: pointer;
}

.video-container .scroll-btn svg {
  height: 8rem;
  width: 8rem;
  fill: #fff;
}

.video-container .video-headings {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.video-container .video-headings .heading__primary--main {
  margin-bottom: 1rem;
}

.header {
  position: relative;
  height: 10vh;
  padding: 2rem;
  z-index: 2;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.header .menu-svg {
  width: 4rem;
  height: 4rem;
  fill: #fff;
}

.header .menu-icon {
  cursor: pointer;
  display: flex;
  align-items: center;
  align-content: center;
}

.header .menu-icon span {
  margin-right: 1rem;
  font-size: 2rem;
  color: #fff;
}

.header .nav-container {
  padding: 2rem 0;
  text-align: right;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background-image: url(../images/nav-bg.jpg);
  height: 100%;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: 100vh;
  display: none;
  opacity: 1;
}

.header .nav-container .nav-icon {
  width: 4rem;
  height: 4rem;
  fill: #fff;
  cursor: pointer;
}

#menu-main-navigation {
  font-size: 6rem;
  font-weight: 700;
  list-style: none;
}

#menu-main-navigation .menu-item a {
  color: #fff;
  border-bottom: 4px solid transparent;
  transform: all .4s ease-out;
}

#menu-main-navigation .menu-item a:hover {
  border-bottom: 4px solid #fff;
}

.footer {
  color: #f7f7f7;
  background-color: #111;
  padding: 10rem 0;
  font-size: 2rem;
}

.footer h4 {
  margin-bottom: 2rem;
}

.footer ul {
  list-style: none;
  font-size: 1.6rem;
  font-weight: thin;
}

.footer ul a {
  color: #fff;
}

.footer .contact a {
  display: flex;
  align-content: center;
  align-items: center;
}

.footer .contact svg {
  height: 1.6rem;
  width: 1.6rem;
  fill: #fff;
  margin-right: 1rem;
}

.footer .connect ul {
  display: flex;
  grid-gap: 2rem;
}

.footer .connect svg {
  height: 3rem;
  width: 3rem;
}
